* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Raleway', sans-serif;
}

body {
	background-color: #e8ffe8;
}

.header-bar {
	background-color: #00e0ff;
	color: white;
	font-family: 'Raleway', sans-serif;
	padding-top: 1vh;
	border-bottom: 5px solid #74f9ff;
}

.weather-content-style {
	background-color: #a6fff2;
	min-width: 100%;
	text-align: center;
	padding: 1em;
}

.gif {
	min-width: 100%;
	max-height: 32em;
}
